import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { SplitText } from "gsap/SplitText";

gsap.registerPlugin(ScrollTrigger, SplitText);

let parent = new SplitText(".o-block-text h2", {type: "lines", linesClass: "line-parent"});
let child = new SplitText(".o-block-text h2 > .line-parent", {type: "lines", linesClass: "line-child"});

let parent2 = new SplitText(".js-text", {type: "lines", linesClass: "line-parent"});
let child2 = new SplitText(".js-text .line-parent", {type: "lines", linesClass: "line-child"});


