gsap.from(".js-image", {
    scrollTrigger: ".js-block",
    y: "10%",
    opacity: 0,
    ease: "Power4.in",
    duration: .9,
})

gsap.utils.toArray(".js-block").forEach(section => {
    gsap.from(section.querySelectorAll(".line-child"), {
    scrollTrigger:{
        trigger: section
    },
    y: "100%",
    delay: .5,
    duration: 1.2,
    ease: "Power4.easeOut",
    stagger: 0.1
    })
});

gsap.utils.toArray(".js-text").forEach(section => {
    gsap.from(section.querySelectorAll(".line-child"), {
    scrollTrigger:{
        trigger: section
    },
    y: "100%",
    duration: .8,
    ease: "Power4.easeOut",
    stagger: 0.1
    })
});

gsap.utils.toArray(".js-block").forEach(section => {
    gsap.to(section.querySelector(".o-cover"), {
    scrollTrigger:{
        trigger: section
    },
    y: "-100%",
    duration: .8,
    delay: .1,
    ease: "Power4.easeOut"
    })
});

gsap.to(".c-prog", {
    scrollTrigger:{
        trigger: "body",
        scrub: true,
        start: "5",
        end: "bottom-=600"
        // pin: true
    },
    scaleX: "100%",
    ease: "Power4.in"
    })


function cook_hide(){
    document.querySelector('.c-cookies').style.display = "none";
}

let icnMenu = document.querySelector('.menu-icon');
let menu = document.querySelector('.js-menu');
let fitmenu = document.querySelector('.c-fit-menu');

icnMenu.addEventListener('click', () => {
	icnMenu.classList.toggle('active');
    // menu.classList.toggle("--visible");
    fitmenu.classList.toggle("--visible");
});


document.querySelector('.c-cookies > a').addEventListener('click', () => {
	document.querySelector('.c-cookies').style.display = "none";
});



